@import url("https://fonts.googleapis.com/css?family=VT323&display=swap");

* {
  --font-size: 1.5rem;
  --green: #4AF626;

  margin: 0;
  padding: 0;

  font-family: "VT323", monospace;
  text-transform: uppercase;
  box-sizing: border-box;

  font-size: var(--font-size);
  color: var(--green);
}
